export default {
        data() {
            return {
                x:0,
                y:0,
                x1:0,
                x2:0,
                y1:0,
                y2:0,
                move:{
                    x:0,
                    y:0
                }
            }
        },
        name: "kefu",
        props: {
            qq: {
                type: Number,
                default: null
            },
            damping:{
                type: Number,
                default: 10
            },
            direction:{
                type: String,
                default: "all"
            },
            position:{
                type: Number,
                default: 4
            }
        },
        methods: {
            onChange(e){
                if(e.detail.source==="touch"){
                    this.move.x=e.detail.x;
                    this.move.y=e.detail.y;
                }
				
            },
            onTouchend(){
                this.x=this.move.x;
                this.y=this.move.y;
				console.log(this.x2)
                setTimeout(()=>{
                    if(this.move.x<this.x2/2) this.x=this.x1;
                    else this.x=this.x2;
					
					if(this.move.y>215) this.y=215;
					
					
                    console.log(this.x,this.y)
                },100)
            },
            onTap(){



















                console.log("tap")
            }
        },
	}
